.r-container {
  height: calc(100vh - 56px);
}

.cubo {
  display: grid;
  grid-template-rows: 100px 100px 100px;
  grid-template-columns: 100px 100px 100px;

  border-radius: 10px;
}

.colores {
  border: 1px solid #2a2a2a;
  border-radius: 10px;
}

.colores:nth-child(1) {
  background: #fc5c65;
}
.colores:nth-child(2) {
  background: #ffffff;
}

.colores:nth-child(3) {
  background: #fd9644;
}

.colores:nth-child(4) {
  background: #26de81;
}
.colores:nth-child(5) {
  background: #26de81;
}

.colores:nth-child(9) {
  background: #26de81;
}

.colores:nth-child(6) {
  background: #3867d6;
}
.colores:nth-child(7) {
  background: #ffffff;
}

.colores:nth-child(8) {
  background: #fed330;
}

.imagen {
  width: 70%;
}
.contenedor {
  background: #efefbb; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #d4d3dd,
    #efefbb
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #d4d3dd,
    #efefbb
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.c-container {
  background-image: url(./img/background.jpg);
  background-attachment: fixed;
}

@media screen and (max-width: 992px) {
  .cubo {
    display: grid;
    grid-template-rows: 70px 70px 70px;
    grid-template-columns: 70px 70px 70px;

    border-radius: 10px;
  }
}
